import { Injectable } from '@angular/core';
 
import { AngularFirestore } from '@angular/fire/firestore';
 
@Injectable({
  providedIn: 'root'
})
export class CrudService {
 
  constructor(
    private firestore: AngularFirestore
  ) { }
 
 
 
  read_Players() {
    return this.firestore.collection('dudes').snapshotChanges();
  }
 
  update_Player(recordID,record){
    // Convert object into array to push into firestore
    var arr = Object.values(record);
    // console.log(arr, typeof arr)
    this.firestore.doc('dudes/' + recordID).set({
        players: arr});
  }
  
}