import { Component, OnInit } from '@angular/core';
import { CrudService } from './service/crud.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./normalize.css', './app.component.css']
})
export class AppComponent  implements OnInit {
  title = 'NBA Player Selector';
  player: any;
  teams: any;
  
  constructor(private crudService: CrudService) { }

  ngOnInit() {
    this.crudService.read_Players().subscribe(data => {
      this.teams = data.map(e => {
        return {
          id: e.payload.doc.id,
          isEdit: false,
          players: e.payload.doc.data()['players'],
        };
      })
      console.log(this.teams);

    });
  }

  EditRecord(record) {
    record.isEdit = true;
    record.editPlayer1 = record.players[0];
    record.editPlayer2 = record.players[1];
    record.editPlayer3 = record.players[2];
    record.editPlayer4 = record.players[3];
    record.editPlayer5 = record.players[4];
    record.editPlayer6 = record.players[5];
    record.editPlayer7 = record.players[6];
    record.editPlayer8 = record.players[7];
    record.editPlayer9 = record.players[8];
    record.editPlayer10 = record.players[9];
    
  }

  UpdateRecord(recordRow) {
    let record = {
      0: recordRow.editPlayer1,
      1: recordRow.editPlayer2,
      2: recordRow.editPlayer3,
      3: recordRow.editPlayer4,
      4: recordRow.editPlayer5,
      5: recordRow.editPlayer6,
      6: recordRow.editPlayer7,
      7: recordRow.editPlayer8,
      8: recordRow.editPlayer9,
      9: recordRow.editPlayer10
    }

    this.crudService.update_Player(recordRow.id, record);
    recordRow.isEdit = false;
  }
  async searchPlayer(name) {
    this.player = await fetch('https://www.balldontlie.io/api/v1/players?search=' + name, {
      mode: 'cors',
      method: "GET",
      headers: {     
      "accept-encoding": "Accepflate, sdch",
      "accept-language": "he-IL,he;q=0.8,en-US;q=0.6,en;q=0.4",
      "cache-control": "max-age=0",
      connection: "keep-alive",
      },
  }).then(function(response) {
        return response.json();
      })
      .then(function(response) {
        let playerID = response.data[0].id
        let firstName = response.data[0].first_name
        let lastName = response.data[0].last_name
        let team = response.data[0].team.full_name
        let playerName = firstName.concat(' ', lastName)
        return playerName + "    " + "-" + "    " + team + "    " + "-" + "    " + "Player ID:" + " " + playerID;
      })
      .catch(function() {
          return "Player doesn't exist"
        });
  }
  
}
